@import "src/common/styles/sass/variables.scss";

.contactsBlock{
    /*background-color: #f8f9fa;*/
    margin-bottom: $marginBottomBlock;
}

.contactsContainer{
    flex-direction: column;
    justify-content: flex-start;
}

.title{
    background-color: #6e939f;
}

.contactsForm {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ccc0c0;
}

.contactsForm input, .contactsForm textarea{
    width: 100%;
}

.submitBtn{
    background-color: #d5cdcd;
    padding: 10px 20px;
}

@media screen and (max-width: 650px) {
    .contactsForm {
        width: 100%;
    }
}